<template lang="pug">
  div(class="planet-info-root")
    span {{user.username}}'s
    h1 {{planet.name}}
    div
      img(:src="transformImage.planet(planet.type)")
    div(class="brief")
      div(class="heading-default")
        h4 簡介
      p {{planetInfo[planetNum].description}}
      div(class="buff")
        div
          i
          span 稀有礦產 : {{planetInfo[planetNum].specialMineral}}
        div
          i
          span 軍事戰力+10%
    div(class="evaluation")
      div(class="heading-default")
        h4 星球評比
      div(class="evaluation-grid")
        div(v-for="(item,index) in planetValue" class="planet-level")
          i(:style="{'background-image':`url(${transformImage.planetInfoIcon(item.name)})`}")
          span {{item.name}}
          span {{item.value}}
        //- div(class="user-chart")
        //-   develop-chart
        div(class="attribute-judge")
</template>

<script>
import DevelopChart from '@/components/home/DevelopChart.vue'
import transformImage from '@/assets/javascript/transformImage'
import planetInformation from '@/assets/data/planet-info.json'

export default {
  components: {
    DevelopChart
  },
  props: {
    user: Object,
    planet: Object
  },
  data: function () {
    return {
      transformImage: transformImage,
      planetValue: [
        { name: '星球等級', value: 'Lv.10' },
        { name: '星球規模', value: '12' },
        { name: '人口數量', value: '2000' },
        { name: '戰力指數', value: '220' },
        { name: '已解鎖建築', value: '12' },
        { name: '已召喚腳色', value: '2' }
      ],
      planetNum: 0,
      planetInfo: planetInformation.planetInfo
    }
  },
  created () {
    switch (this.planet.type) {
      case '赫克斯':
        this.planetNum = 0
        break
      case '奎奈爾':
        this.planetNum = 1
        break
      case '蒂瑪修':
        this.planetNum = 2
        break
      case '墨菲比斯':
        this.planetNum = 3
        break
    }
  }
}
</script>

